import { getKeyString } from './lib/keys'

export default function Tracklist({ tracks }) {
  return (
    <table className="tracks">
      <thead>
        <tr>
          <th>Intro?</th>
          <th>Artist</th>
          <th>Title</th>
          <th>Key</th>
          {tracks[0]
            ? Object.keys(tracks[0].properties).map((propertyName) => (
                <th>{propertyName.replace(/^./, propertyName[0].toUpperCase())}</th>
              ))
            : null}
        </tr>
      </thead>
      <tbody>
        {tracks.map((track, i) => (
          <tr>
            <td>
              <input type="checkbox" data-intro-track-id={i} />
            </td>
            <td>{track.artist}</td>
            <td>{track.title}</td>
            <td>{getKeyString(track.keyNumber, track.isMinor)}</td>
            {Object.values(track.properties).map((property) => (
              <td>{property}</td>
            ))}
          </tr>
        ))}
      </tbody>
    </table>
  )
}
